.about {
  padding: 15rem 0 15rem;
  color: $black;

  &__wrapper {
    display: flex;
    justify-content: center;
    max-width: 113.8rem;
    margin: 0 auto;
  }

  &__content {
    max-width: 56rem;
    padding: 0.8rem 0 0;
  }

  &__title {
    font-family: $dele;
    font-weight: 400;
    font-size: 2.5rem;
    line-height: 1.4;
    margin: 0 0 3.5rem;
  }

  &__description {
    font-weight: 300;
    font-size: 2.5rem;
    line-height: 3rem;
    margin: 0 0 4rem;
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    margin: 0 0 4rem;
  }

  &__item {
    position: relative;
    font-weight: 500;
    font-size: 2.5rem;
    line-height: 3rem;
    margin: 0 0 1.5rem;

    &:before {
      content: '';
      position: absolute;
      top: -0.5rem;
      left: -4.6rem;
      width: 4rem;
      height: 4rem;
      background-image: url('data:image/png;base64,');
      background-repeat: no-repeat;
      background-size: cover;
    }

  };

  &__item-heart {
    display: inline-block;
    position: relative;

    svg {
      position: absolute;
      bottom: 0.5rem;
      right: -2.9rem;
      transform: rotate(23deg);
    }
  }

  &__star-icon {
    position: absolute;
  }


  &__button {
    width: 36.5rem;
    font-size: 2.5rem;
    line-height: 3rem;

  }

}
